const zhHans = {
  prev: '',
  next: '',
  go: '',
  jump: ''
}

const zhHant = {
  prev: '',
  next: '',
  go: '',
  jump: ''
}

const en = {
  prev: 'Prev',
  next: 'Next',
  go: 'Go',
  jump: 'Jump To'
}

const ja = {
  prev: '',
  next: '',
  go: '',
  jump: ''
}

const ko = {
  prev: ' ',
  next: ' ',
  go: '',
  jump: ''
}

export default function pagationLocales (ctx) {
  const { $lang, $recoLocales: { pagation } = {} } = ctx
  if (pagation) {
    return pagation
  }
  if (/^zh\-(CN|SG)$/.test($lang)) {
    return zhHans
  }
  if (/^zh\-(HK|MO|TW)$/.test($lang)) {
    return zhHant
  }
  if (/^ja\-JP$/.test($lang)) {
    return ja
  }
  if (/^ko\-KR$/.test($lang)) {
    return ko
  }
  return en
}
